* {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.menu,
.menu-bg,
.menu-burger {
  position: fixed;
  width: 50px;
  height: 50px;
  font-size: 30px;
  text-align: center;
  border-radius: 100%;
  right: 25px;
  top: 25px;
  z-index: 1000;
}

@media (max-width: 530px) {
  .menu-burger {
    top: 10px;
    right: 10px;
  }
}

.c-white {
  color: white !important;
}

.menu-bg {
  pointer-events: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  right: 50px;
  top: 50px;
  -webkit-transform: translate3d(50%, -50%, 0);
          transform: translate3d(50%, -50%, 0);
  -webkit-transform-origin: center center;
          transform-origin: center center;
}
.menu-bg.fs {
  -webkit-transform: translate3d(50%, -50%, 0);
          transform: translate3d(50%, -50%, 0);
  width: 300vw;
  height: 300vw;
  background: #663399;
}

.menu-burger {
  color: #630092;
  padding-top: 8px;
  -webkit-user-select: none;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  -webkit-transform-origin: center;
          transform-origin: center;
}
.menu-burger.fs {
  -webkit-transform: rotate(-180deg) translateY(10px);
          transform: rotate(-180deg) translateY(10px);
}

.menu-items {
  position: absolute;
  font-weight: bold;
  font-size: 40px;
  letter-spacing: 10px;
  color: white;
  width: 100%;
  text-align: center;
  opacity: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  margin-top: 0px;
  -webkit-transform: translateY(-200%);
          transform: translateY(-200%);
  pointer-events: none;
}
.menu-items div {
  padding: 0;
  -webkit-transition: 1s;
  transition: 1s;
  opacity: 0;
  margin-top: 0px;
}
.menu-items.fs {
  z-index: 1000;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  pointer-events: auto;
  opacity: 1;
  width: 100%;
  height: 100%;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.menu-items.fs div {
  opacity: 1;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 20px;
}

.menu-items div:hover {
  cursor: pointer;
}

.menu-burger:hover {
  cursor: pointer !important;
}

.main-header {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: cover;
}
.main-header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: -webkit-linear-gradient(
    36deg,
    rgba(74, 76, 179, 0.95) 34%,
    hsla(0, 34%, 71%, 0.95)
  );
  background-image: linear-gradient(
    54deg,
    rgba(74, 76, 179, 0.95) 34%,
    hsla(0, 34%, 71%, 0.95)
  );
  opacity: 0.9;
}

.header-outer {
  position: relative;
}

.header-wrapper {
  display: -webkit-flex;
  display: flex;
  margin-left: 8vw;
}

.profile-pic {
  -webkit-align-self: center;
          align-self: center;
  width: 350px;
  border-radius: 5px;
  box-shadow: 0 0 27px rgba(96, 96, 96, 0.34);
  margin-top: 10px;
  top: 40px;
  position: relative;
}

.header-data {
  color: white;
  margin-left: 5vw;
}

.header-data .name {
  margin-top: 20px;
  font-size: 44px;
  letter-spacing: 3px;
  margin-bottom: 10px;
  font-weight: 700;
}

.header-data .role {
  font-size: 16px;
  margin-top: 0;
  letter-spacing: 2px;
  margin-bottom: 40px;
}

.general-data {
  margin-bottom: 20px;
}

.general-data .list dt {
  float: left;
  opacity: 0.9;
  font-weight: 700;
  text-transform: capitalize;
  font-size: 18px;
}

.general-data .list dd {
  font-size: 18px;
  padding-left: 110px;
  margin-bottom: 25px;
  color: white;
  letter-spacing: 1px;
}

.general-data .list dd a {
  color: white;
  text-decoration: none;
}

@media (max-width: 936px) {
  .profile-pic {
    display: none;
  }
  .header-data {
    margin-left: 0;
  }
}

@media (max-width: 530px) {
  .header-data .name {
    font-size: 8vw;
    margin-bottom: 2px;
  }
  .header-data .role {
    display: block;
    font-size: 15px;
    padding-right: 140px;
    margin-bottom: 20px;
  }
  .general-data .list dd {
    font-size: 15px;
    margin-bottom: 15px;
    padding-left: 30px;
  }
  .general-data .list dt {
    font-size: 15px;
  }
}

.social-profile-data {
  margin-bottom: 20px;
}

.social-profile-data a {
  font-size: 24px;
  margin-right: 25px;
  opacity: 0.5;
  color: white !important;
}

.social-profile-data a:hover {
  opacity: 1;
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
}

